import cookies from "vue-cookies";
export default {
  methods: {
    confirmLogin() {
      let res = confirm("Please login your account.");
      if (res == true) {
        this.$router.push("/login");
      }
    },
    toUserPage() {
      if (cookies.get("user")) {
        const user_id = cookies.get("user").user_id;
        if (user_id) {
          this.$router.push(`/user/${user_id}`);
        }
      } else {
        this.confirmLogin();
      }
    },
    share() {
      if (cookies.get("user")) {
        this.$router.push(`/share`);
      } else {
        this.confirmLogin();
      }
    },
  },
};
